	module.exports = {"data":{"markdownRemark":{"html":"<p>のこさず ぜんぶ たべましょう 元ネタは昔アフタヌーンにて連載していたコミック、“ミルク クローゼット”より。普通のロリ萌え系な表紙と裏腹に中身はかなりすごい。  少年が宇宙人に銛で刺されたり女の子が機械でミンチにされたりと、もう暴力シーンを超えた残虐シーンがあったり。それをグロになりすぎず、そう言う系に抵抗ある人間でもさらっと読めてしまう氏の画風・画力にはただただ脱帽である。単行本が出ているので良かったら一読してみたらどうだろう。 ラストシーンで、女の子が宇宙全てを飲み込んでしまうシーンがあるが、その暴食っぷりをルーミアに置き換えてみた。当初紫はなんかごちゃっとしてて描きづらそうだって言うある種の食わず嫌い感が根付いてましたが、この後まさかアレだけの量の紫を描くとは当時知る由もない。</p>","frontmatter":{"cover":null,"date":"2004-07-16T04:46:00+09:00","strDate":"2004/7/15","title":"るーみあの おしょくじ"}}},"pathContext":{"path":"/ja/blog/2004/07/16-東方-るーみあの-おしょくじ/","langKey":"ja"}}

